import React, { Component } from 'react'
// import { withRouter } from 'react-router-dom'
import Form from '../../@components/Form';
import ConfigService from '../../@services/Config';
import Code from '../../@components/Code';
import Busy from '../../@components/Busy';

import { SideSheet, Pane, Dialog, EditIcon, TickIcon, Alert, TrashIcon, Heading, Paragraph, Button, FloppyDiskIcon, toaster } from 'evergreen-ui'

class PageConfig__SchemaEdit extends Component {

  constructor(props)
  {
    super(props)
    this.state = {
      ready: false,
      busy: true,
      saveBusy: false,
      validateBusy: false,
      isDeleteShown: false,
      isShown: false,
      changes: false,
      formErrors: []
    }
    this.mounted = false
    this.saveDocument = this.saveDocument.bind(this)
    this.getSchemaValue = this.getSchemaValue.bind(this)
    this.showJson = this.showJson.bind(this)
    this.validateJson = this.validateJson.bind(this)
    this.removeError = this.removeError.bind(this)
    this.updateFormData = this.updateFormData.bind(this)
  }
  
  async componentDidMount()
  {

    this.mounted = true
    
    this.getSchemaValue()

  }

  getSchemaValue()
  {

    var isNew = this.props.match?.params.id === "new"

    ConfigService.getSchema(this.props.schemaKey)
      .then(({ documentation, schema, supportsTemplates, table }) => {

        if (!this.mounted) return;

        ConfigService.getValue(this.props.schemaKey)
          .then(SchemaValue => this.props?.isSingular ? {} : SchemaValue.filter(item => item.key === this.props.match.params?.id))
          .then(SchemaValue => this.props?.isSingular ? {} : (SchemaValue.length ? SchemaValue[0] : false))
          .then(SchemaValue => isNew ? {} : SchemaValue)
          .then(SchemaValue => {

            if (!this.mounted) return;
            
            if (!SchemaValue && !isNew) return;

            var SchemaFormValue = {}

            Object.keys(schema).forEach(key => {

              SchemaFormValue[key] = {
                ...schema[key],
                key: key,
                errors: [],
                onChange: (key, value) => {
                  this.updateFormData({...this.state.formData, [key]: { ...this.state.formData[key], value: value }})
                },
                value: (() => {
                  
                  if (!(key in SchemaValue) || SchemaValue[key] === undefined) return schema[key].default || undefined;

                  switch (schema[key]?.type)
                  {
                    case 'string[]':
                      return SchemaValue[key].map(item => { return typeof item === "string" ? (item) : (`${item.id}@${item.version}`) });
                    case 'keys':
                      return (() => {
                        var Values = {}
                        Object.keys(schema[key].keys).forEach(k => {
                          var Value = (k in SchemaValue[key]) ? SchemaValue[key][k] : (schema[key][k]?.default);
                          if (undefined !== Value) Values[k] = Value;
                        })
                        return Values
                      })();
                    default:
                      return SchemaValue[key];
                  }

                })()
              }

              // post value set field mods
              if (schema[key]?.type === 'repeater')
              {
                SchemaFormValue[key].columns = 'columns' in SchemaFormValue[key] ? SchemaFormValue[key].columns : [{label:'Key', key: 'key'}];
              }

            })

            this.setState({
              isNew: isNew,
              supportsTemplates: supportsTemplates,
              formData: SchemaFormValue,
              saveBusy: false,
              busy: false,
              ready: true
            })

          })
        
      })
  }

  componentWillUnmount()
  {
    this.mounted = false
  }

  validateJson(e)
  {
    e.preventDefault()
    this.setState({
      validateBusy: true
    })
    ConfigService.validateValue(this.props.schemaKey, this.state.formData)
      .then(res => {

        if (!this.mounted) return;

        if (res?.errors) {
        
          toaster.danger(`${res.errors.length} Error(s)`)

          this.setState({
            formErrors: res.errors,
            validateBusy: false
          })

          return;

        }
        
        toaster.success('Valid ðŸ‘')

        this.setState({
          validateBusy: false
        })

      })
  }

  saveDocument(e)
  {
    e.preventDefault()
    this.setState({
      saveBusy: true
    })
    ConfigService.setValue(this.props.schemaKey, this.state.formData)
      .then((res)=>{

        if (!this.mounted) return;

        if (res?.errors) {
        
          toaster.danger(`${res.errors.length} Error(s)`)

          this.setState({
            formErrors: res.errors,
            saveBusy: false
          })

          return;

        }
        
        toaster.success('Saved')

        this.setState({
          saveBusy: false
        })

        this.getSchemaValue()

      })
  }

  showJson(e)
  {
    e.preventDefault();
    var jsonObj = {}
    Object.keys(this.state.formData).forEach(key => {
      if (undefined !== this.state.formData[key].value) {
        if (Array.isArray(this.state.formData[key].value) && this.state.formData[key].value.length === 0) return;
        jsonObj[key] = this.state.formData[key].value
      }
    })
    this.setState({
      formString: JSON.stringify(jsonObj, null, 4),
      isShown: true
    })
  }

  removeError(index)
  {
    var formErrors = this.state.formErrors.splice(index, 1)
    formErrors.splice(index, 1)
    this.setState({
      formErrors: formErrors
    })
  }

  updateFormData(data)
  {
    this.setState({
      formData: data,
      changes: true
    })
  }

  render() {
    return (
      <article>
        {this.state.ready?(
          <section className="split-content">
            <div className="split-content__left">
              {(!this.state.busy)?([
                <Pane display="flex" padding=".5em" background="tint2">
                  <Pane flex={1} alignItems="center" display="flex">
                    <Heading size={600}>{`${this.props.schemaKey} Config`}</Heading>
                  </Pane>
                  <Pane>
                    <Button appearance="minimal" onClick={this.validateJson} isLoading={this.state.validateBusy} iconBefore={this.state.validateBusy?(null):(TickIcon)}>Validate</Button>
                  </Pane>
                </Pane>,
                <Form className="split-content__form" data={this.state.formData}></Form>
              ]):<Busy/>}
            </div>
            <div className="split-content__right">
              <Pane padding='.5em' backgroundColor='white' borderBottom="muted">
                <Button disabled={!this.state.changes} appearance="minimal" onClick={this.saveDocument} isLoading={this.state.saveBusy} iconBefore={this.state.saveBusy?(null):(FloppyDiskIcon)}>{this.state.isNew?'Save':'Update'}</Button>
                <Button appearance="minimal" iconBefore={EditIcon} onClick={this.showJson}>View Json</Button>
                {!this.state.isNew?(<Button appearance="minimal" intent="danger" marginRight={'.5em'} iconBefore={TrashIcon} onClick={(e) => { e.preventDefault(); this.setState({ isDeleteShown: true })}}>Delete</Button>):(null)}
              </Pane>
              {this.state.formErrors.map((err, index)=>(
                <Alert isRemoveable={true} onRemove={()=>{this.removeError(index)}} key={index} intent="danger" title={err} marginBottom={15} />
              ))}
              <Pane padding="1em">
                <Heading size={400}>What is this?</Heading>
                <Paragraph size={400} color="muted">
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam laoreet non augue et faucibus. Ut arcu tellus, hendrerit id sem in, iaculis varius ipsum. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Etiam ac aliquet orci. Donec porta risus at nisi placerat pellentesque.
                </Paragraph>
              </Pane>
            </div>
            {this.state.saveBusy||this.state.validateBusy?(<div className="content-busy"></div>):null}
          </section>
        ):null}
        <SideSheet
          isShown={this.state.isShown}
          onCloseComplete={() => this.setState({ isShown: false })}
        >
          <Pane padding='.5em' backgroundColor='white' borderBottom="muted">
            <Button disabled={true} appearance="minimal" iconBefore={EditIcon}>Edit</Button>
          </Pane>
          <Code codeString={this.state.formString} />
        </SideSheet>
        <Dialog
          isShown={this.state.isDeleteShown}
          hasHeader={false}
          intent="danger"
          isConfirmLoading={this.state.busy}
          onConfirm={() => {
            this.setState({ busy: true })
            ConfigService.deleteValue(this.props.schemaKey, this.state.formData.key.value)
              .then(res => {
                this.setState({ isDeleteShown: false })
                this.props.history.goBack()
                toaster.success(
                  'Removed Successfully'
                )
              })
          }}
          onCloseComplete={() => this.setState({ isDeleteShown: false, busy: false })}
          confirmLabel="Delete"
        >
          <Heading size={600}>Are you sure?</Heading>
          <Paragraph size={400} color="muted">
            Removing this {this.props.schemaPath} may effect other components that require it.
          </Paragraph>
        </Dialog>
      </article>
    )
  }
}

export default PageConfig__SchemaEdit;
